@charset "UTF-8";
/**
    リセットと初期設定を両方記載
    共通パーツの指定 section__area section__title inner など
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");
*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

html, body {
  padding: 0;
  margin: 0;
}

body {
  color: #333;
  font-size: 2rem;
  font-family: "Noto Sans JP", sans-serif;
  letter-spacing: 0.2rem;
}

@media screen and (max-width: 767px) {
  body {
    font-size: 1.2rem;
    letter-spacing: 0.5rem;
  }
}
img {
  max-width: 100%;
  width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6,
ul, li, ol, small, b, a,
strong, dl, dt, dd {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

.section__title {
  font-weight: bold;
  font-size: 3.6rem;
  text-align: center;
  margin-bottom: 80px;
  position: relative;
  padding-bottom: 40px;
  border-radius: 999px 999px 0 0;
  background: #00ced1;
}

.section__title:nth-child(2n) {
  border-radius: 0 999px 0 999px;
}

.section__title span {
  position: relative;
  position: absolute;
  content: "";
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.6rem;
}

.section__title span::before {
  position: absolute;
  content: "";
  width: 100%;
  top: -30%;
  left: 0;
  height: 4px;
  background: #fff;
  border-radius: 999px;
}

@media screen and (max-width: 767px) {
  .section__title span::before {
    top: -40%;
    height: 2px;
  }
}
@media screen and (max-width: 767px) {
  .section__title span {
    font-size: 1rem;
  }
}
@media screen and (max-width: 767px) {
  .section__title {
    font-size: 2.4rem;
    margin-bottom: 30px;
    margin-top: 32px;
    padding-bottom: 30px;
  }
}
.section__area {
  padding: 100px 0;
}

.section__area:nth-child(2n+1) {
  background: url(../images/reLpbg05.jpg) no-repeat center center/cover;
}

.section__area:nth-child(2n) {
  background: url(../images/reLpbg10.jpg) no-repeat center center/cover;
}

@media screen and (max-width: 767px) {
  .section__area {
    padding: 10px 10px;
  }
  .section__area:not(:first-of-type) {
