.header {
  position: fixed;
  z-index: 500;
}

.header__logo {
  width: 200px;
  margin: auto 0;
  background: transparent;
}

.header__logo img {
  width: 100%;
}

.header .header__btn {
  width: 60px;
  height: 60px;
  top: 50px;
  right: 50px;
  transition: 0.6s;
  position: relative;
  position: fixed;
  z-index: 300;
}

@media screen and (max-width: 767px) {
  .header .header__btn {
    width: 30px;
    height: 30px;
    top: 20px;
    right: 20px;
  }
}
.header .header__btn span {
  position: absolute;
  width: 100%;
  background-color: #000;
  height: 2px;
  transition: ease 0.4s;
  display: block;
}

.header .header__btn span:nth-of-type(1) {
  top: 0;
}

.header .header__btn span:nth-of-type(2) {
  top: 50%;
}

.header .header__btn span:nth-of-type(3) {
  bottom: 0;
}

.header .header__btn.active {
  transition: 0.6s;
}

.header .header__btn.active > span {
  background: #fff;
  height: 4px;
  border-radius: 10px;
}

.header .header__btn.active > span:nth-of-type(1) {
  top: 50%;
  transform: rotate(45deg);
}

.header .header__btn.active > span:nth-of-type(2) {
  display: none;
}

.header .header__btn.active > span:nth-of-type(3) {
  top: 50%;
  transform: rotate(-45deg);
}

.header__nav {
  display: none;
  position: fixed;
  height: 100%;
  top: 0;
  padding: 0;
  width: 100%;
  z-index: 200;
}

.header__nav.active {
  display: block;
}

.header__nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.header__nav ul li a {
