    padding: 10px 0;
  }
}
.header__nav ul li a:hover {
  text-decoration: underline;
  background: red;
  color: #fff;
}

.header__bg.active {
  z-index: 100;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: black;
  opacity: 0.7;
  transition: 0.6s;
}

.top {
  position: relative;
}

.top img {
  height: 100%;
}

.top__text {
  font-size: 3.2rem;
  font-weight: bold;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
